"use client"

import { useState, useEffect } from "react"
import { Header } from "@/components/header"
import { PokemonList } from "@/components/pokemon-list"
import { PokemonDetails } from "@/components/pokemon-details"
import type { Pokemon } from "@/types/pokemon"

export default function Home() {
  const [pokemonList, setPokemonList] = useState<Pokemon[]>([])
  const [selectedPokemon, setSelectedPokemon] = useState<Pokemon | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [favorites, setFavorites] = useState<number[]>([])
  const [showFavoritesOnly, setShowFavoritesOnly] = useState(false)
  const [searchTerm, setSearchTerm] = useState("")

  // Load initial Pokemon list
  useEffect(() => {
    const loadPokemon = async () => {
      try {
        setLoading(true)
        setError(null)
        const response = await fetch("https://pokeapi.co/api/v2/pokemon?limit=150")
        const data = await response.json()

        const pokemonDetails = await Promise.all(
          data.results.map((p: any) => fetch(`https://pokeapi.co/api/v2/pokemon/${p.name}`).then((r) => r.json())),
        )

        setPokemonList(pokemonDetails)
      } catch (err) {
        setError("Could not load Pokémon. Retry?")
      } finally {
        setLoading(false)
      }
    }

    loadPokemon()
  }, [])

  // Load favorites from localStorage
  useEffect(() => {
    const saved = localStorage.getItem("pokemonFavorites")
    if (saved) {
      setFavorites(JSON.parse(saved))
    }
  }, [])

  // Save favorites to localStorage
  useEffect(() => {
    localStorage.setItem("pokemonFavorites", JSON.stringify(favorites))
  }, [favorites])

  const handleToggleFavorite = (id: number) => {
    setFavorites((prev) => (prev.includes(id) ? prev.filter((f) => f !== id) : [...prev, id]))
  }

  const filteredPokemon = pokemonList
    .filter((p) => !showFavoritesOnly || favorites.includes(p.id))
    .filter((p) => p.name.toLowerCase().includes(searchTerm.toLowerCase()))

  return (
    <div className="min-h-screen bg-slate-50 dark:bg-slate-950">
      <Header
        showFavoritesOnly={showFavoritesOnly}
        onToggleFavorites={setShowFavoritesOnly}
        searchTerm={searchTerm}
        onSearchChange={setSearchTerm}
      />

      <div className="flex h-[calc(100vh-80px)]">
        <PokemonList
          pokemon={filteredPokemon}
          selectedPokemon={selectedPokemon}
          favorites={favorites}
          loading={loading}
          error={error}
          onSelectPokemon={setSelectedPokemon}
          onToggleFavorite={handleToggleFavorite}
        />

        <PokemonDetails
          pokemon={selectedPokemon}
          isFavorite={selectedPokemon ? favorites.includes(selectedPokemon.id) : false}
          onToggleFavorite={() => selectedPokemon && handleToggleFavorite(selectedPokemon.id)}
        />
      </div>
    </div>
  )
}
