"use client"

import { Button } from "@/components/ui/button"
import { Star } from "lucide-react"
import type { Pokemon } from "@/types/pokemon"

interface PokemonDetailsProps {
  pokemon: Pokemon | null
  isFavorite: boolean
  onToggleFavorite: () => void
}

export function PokemonDetails({ pokemon, isFavorite, onToggleFavorite }: PokemonDetailsProps) {
  if (!pokemon) {
    return (
      <div className="hidden md:flex w-[65%] flex-col items-center justify-center bg-slate-50 dark:bg-slate-950 text-slate-500">
        <p>Select a Pokémon to view details</p>
      </div>
    )
  }

  const types = pokemon.types.map((t) => t.type.name)
  const abilities = pokemon.abilities.map((a) => a.ability.name)

  return (
    <div className="hidden md:flex w-[65%] overflow-y-auto bg-slate-50 dark:bg-slate-950">
      <div className="w-full p-8 space-y-6">
        {/* Sprite & Name */}
        <div className="flex flex-col items-center gap-4">
          <img
            src={pokemon.sprites.front_default || "/placeholder.svg?height=150&width=150"}
            alt={pokemon.name}
            className="w-32 h-32"
          />
          <h1 className="text-4xl font-bold capitalize text-center">{pokemon.name}</h1>
          <p className="text-sm text-slate-500">#{pokemon.id}</p>
        </div>

        {/* Favorite Button */}
        <div className="flex justify-center">
          <Button
            onClick={onToggleFavorite}
            variant={isFavorite ? "default" : "outline"}
            className="gap-2 px-6"
            size="lg"
          >
            <Star className="w-5 h-5" fill={isFavorite ? "currentColor" : "none"} />
            {isFavorite ? "Remove Favorite" : "Add to Favorites"}
          </Button>
        </div>

        {/* Type Chips */}
        <div>
          <h2 className="font-semibold text-slate-900 dark:text-slate-100 mb-3">Type</h2>
          <div className="flex flex-wrap gap-2">
            {types.map((type) => (
              <span
                key={type}
                className={`px-3 py-1 rounded-full text-sm font-medium capitalize ${getTypeColor(type)}`}
              >
                {type}
              </span>
            ))}
          </div>
        </div>

        {/* Abilities */}
        <div>
          <h2 className="font-semibold text-slate-900 dark:text-slate-100 mb-3">Abilities</h2>
          <ul className="space-y-2">
            {abilities.length > 0 ? (
              abilities.map((ability) => (
                <li key={ability} className="capitalize text-slate-700 dark:text-slate-300">
                  • {ability}
                </li>
              ))
            ) : (
              <p className="text-slate-500">No known abilities</p>
            )}
          </ul>
        </div>

        {/* Stats */}
        <div>
          <h2 className="font-semibold text-slate-900 dark:text-slate-100 mb-3">Stats</h2>
          <div className="space-y-2">
            {pokemon.stats.map((stat) => (
              <div key={stat.stat.name} className="flex items-center gap-3">
                <span className="w-20 text-sm capitalize text-slate-600 dark:text-slate-400">{stat.stat.name}</span>
                <div className="flex-1 bg-slate-200 dark:bg-slate-800 rounded-full h-2">
                  <div className="bg-blue-500 h-2 rounded-full" style={{ width: `${(stat.base_stat / 255) * 100}%` }} />
                </div>
                <span className="w-12 text-right text-sm font-semibold">{stat.base_stat}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

function getTypeColor(type: string): string {
  const colors: Record<string, string> = {
    normal: "bg-slate-300 text-slate-900",
    fire: "bg-red-400 text-white",
    water: "bg-blue-400 text-white",
    electric: "bg-yellow-400 text-slate-900",
    grass: "bg-green-400 text-white",
    ice: "bg-cyan-400 text-white",
    fighting: "bg-red-700 text-white",
    poison: "bg-purple-500 text-white",
    ground: "bg-amber-600 text-white",
    flying: "bg-indigo-400 text-white",
    psychic: "bg-pink-500 text-white",
    bug: "bg-lime-500 text-slate-900",
    rock: "bg-slate-600 text-white",
    ghost: "bg-purple-700 text-white",
    dragon: "bg-indigo-600 text-white",
    dark: "bg-slate-800 text-white",
    steel: "bg-slate-400 text-slate-900",
    fairy: "bg-pink-400 text-white",
  }
  return colors[type] || "bg-slate-300 text-slate-900"
}
